import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class WhatsappService {

  messageText = "Hola Buenos dias, quisiera agendar una cita";

  phoneNumber = "50364204810"

  linkToWhatsAppMobile = `https://api.whatsapp.com/send?phone=${this.phoneNumber}&text=${this.messageText}`;

  linkToWhatsAppWeb = `https://web.whatsapp.com/send?phone=${this.phoneNumber}&text=${this.messageText}`;


  isMobile() {
    const regex = /Mobi|Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i;
    return regex.test(navigator.userAgent);
  }

  getWhatsAppUrl() {
   return this.isMobile() ? this.linkToWhatsAppMobile : this.linkToWhatsAppWeb;
  }
}
