import { Component } from '@angular/core';
import { ServiceHelperService } from '../../helper/service/service-helper.service';
import { WhatsappService } from '../../helper/whatsapp.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent extends ServiceHelperService {
  whatsAppUrl = '';
	currentYear: number = new Date().getFullYear();
  constructor(private whatsAppService: WhatsappService, route: ActivatedRoute) {
    super(route);
  }

  ngOnInit(): void {
    this.whatsAppUrl = this.whatsAppService.getWhatsAppUrl();
  }
  
}
